import { createMuiTheme } from "@material-ui/core";
import { blue } from "@material-ui/core/colors";

const PitTheme = createMuiTheme({
    palette: {
        primary: {
            light: blue[500],
            main: blue[500],
            dark: blue[500],
        },
        secondary: {
            light: blue[100],
            main: blue[100],
            dark: blue[100],
        },
        action: {
            hover: blue[800],
        },
    },
    overrides: {
        MuiButton: {
            root: {
                "&:hover": {
                    // backgroundColor: "rgba(150, 150, 150, 0.25) !important",
                    color: "white",
                },
            },
        },
    },
});

export default PitTheme;
