import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core";
import StatisticsBlock from "../components/StatisticsBlock";
import MockHeroService from "../services/MockHeroService";
import Hero from "../models/Hero";
import { useParams } from "react-router-dom";
import Trait from "../models/Trait";
import PartyStatistics from "../models/PartyStatistics";
import MockPartyService from "../services/MockPartyService";
import MockWorldService from "../services/MockWorldService";
import WorldStatistics from "../models/WorldStatistics";

const useStyles = makeStyles({
    characterGrid: {
        backgroundColor: "beige",
        fontFamily: "Martel",
        display: "grid",
        gridTemplateColumns: "repeat(2, 1fr)",
        gridAutoRows: "minmax(100px, auto)",
        height: "calc(100vh - 115px)",
    },
    left: {},
    sectionHeading: {
        fontFamily: "MedievalSharp",
    },
    bannerContainer: {
        position: "relative",
        height: "100px",
        top: "-80px",
        zIndex: 1,
    },
    itemContainer: {
        position: "relative",
    },
    close: {
        position: "absolute",
        left: "20px",
        top: "-100px",
        width: "40px",
        filter: "invert(42%) sepia(93%) saturate(1352%) hue-rotate(32deg) brightness(60%) contrast(119%)",
    },
    portrait: {
        backgroundColor: "ivory",
        height: "calc(60vh - 55px)",
        zIndex: 0,
        border: "solid",
        borderWidth: "4px",
        borderColor: "beige",
        borderRadius: "20px",
    },
    portraitImg: {
        position: "absolute",
        left: "20%",
        width: "40%",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "lightslategrey",
        margin: "68px",
    },
    portraitItem: {
        position: "absolute",
        width: "70px",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "6px",
        borderColor: "lightslategrey",
    },
    portraitItemHelm: {
        left: "90px",
        top: "35px",
    },
    portraitItemBreastplate: {
        left: "90px",
        top: "135px",
    },
    portraitItemGloves: {
        left: "90px",
        top: "235px",
    },
    portraitItemLegs: {
        left: "90px",
        top: "335px",
    },
    inventory: {
        height: "calc(40vh - 80px)",
        backgroundColor: "ivory",
        border: "solid",
        borderWidth: "4px",
        borderColor: "beige",
        borderRadius: "20px",
    },
    inventoryGrid: {
        border: "5px solid rgb(111,41,97)",
        display: "grid",
        gridTemplateColumns: "repeat(6, 80px)",
        gridAutoRows: "5em",
        inlineSize: "500px",
        alignItems: "center",
        margin: "0 auto",
    },
    statistics: {
        overflowY: "scroll",
        backgroundColor: "ivory",
        gridRowEnd: "span 2",
        border: "solid",
        borderWidth: "4px",
        borderColor: "beige",
        borderRadius: "20px",
    },
    statisticsBlock: {
        padding: "40px 80px",
    },
});

type HeroProfileProps = {
    //id: string,
};

export function HeroProfile(props: HeroProfileProps) {
    const classes = useStyles();

    const params: any = useParams();
    const id = params.id;

    let [hero, setHero] = useState(null as Hero | null);
    let [party, setParty] = useState(null as PartyStatistics | null);
    let [world, setWorld] = useState(null as WorldStatistics | null);
    let [currentId, setCurrentId] = useState(-1);

    useEffect(() => {
        // Initial load of hero data
        const loadHero = () => {
            const service = new MockHeroService();
            const newHero = service.getHeroDetail(id);

            const partyService = new MockPartyService();
            const newParty = partyService.getParty();

            const worldService = new MockWorldService();
            const newWorld = worldService.getWorld();

            if (!newHero) {
                console.error(`No hero data`);
                return;
            }
            if (!newParty) {
                console.error(`No party data`);
                return;
            }
            if (!newWorld) {
                console.error(`No world data`);
                return;
            }

            console.log("Loading hero");
            setCurrentId(id);
            setHero(newHero);
            setParty(newParty);
            setWorld(newWorld);
        };

        if (currentId !== id) {
            loadHero();
        }
    });

    if (!hero || !party || !world) return null;

    const renderTrait = (t: Trait) => (
        <p>
            {t.name}: {t.from}, {t.behaviour}
        </p>
    );

    return (
        <div className={classes.characterGrid}>
            <div className={classes.left}>
                <div className={classes.portrait}>
                    <br />
                    <div className={classes.bannerContainer}>
                        <h1 className="banner">{hero.name}</h1>
                    </div>
                    <div className={classes.itemContainer}>
                        <a href="/#/">
                            <img src="images/close.svg" className={classes.close} alt="close" />
                        </a>
                        <img src={hero.imageUrl} className={classes.portraitImg} alt="profile" />
                        <img src="images/helm.svg" className={`${classes.portraitItem} ${classes.portraitItemHelm}`} alt="helm" />
                        <img
                            src="images/breastplate.svg"
                            className={`${classes.portraitItem} ${classes.portraitItemBreastplate}`}
                            alt="breastplate"
                        />
                        <img src="images/leg.svg" className={`${classes.portraitItem} ${classes.portraitItemLegs}`} alt="legs" />
                        <img src="images/glove.svg" className={`${classes.portraitItem} ${classes.portraitItemGloves}`} alt="gloves" />
                    </div>
                </div>
                <div className={classes.inventory}>
                    <br />
                    <h1 className={classes.sectionHeading}>Party Inventory</h1>
                    <div className={classes.inventoryGrid}>
                        <div>1</div>
                        <div>2</div>
                        <div>3</div>
                        <div>4</div>
                        <div>5</div>
                        <div>6</div>
                        <div>7</div>
                        <div>8</div>
                        <div>9</div>
                        <div>10</div>
                        <div>11</div>
                        <div>12</div>
                    </div>
                </div>
            </div>
            <div className={classes.statistics}>
                <br />
                <br />
                <h1 className={classes.sectionHeading}>Statistics</h1>
                <div className={classes.statisticsBlock}>
                    <StatisticsBlock statistics={hero.attributes} />
                </div>
                <h1 className={classes.sectionHeading}>Other</h1>
                <div className={classes.statisticsBlock}>
                    <p>Hit Points 10/16</p>
                    <p>Initiative 2</p>
                    <p>Spell Resistance Save 14</p>
                    <p>Witch Taint 12</p>
                </div>
                <h1 className={classes.sectionHeading}>Skills</h1>
                <div className={classes.statisticsBlock}>
                    <p>Persuasion d10</p>
                    <p>Religion d12</p>
                </div>
                <h1 className={classes.sectionHeading}>Traits</h1>
                <div className={classes.statisticsBlock}>{hero.traits.map((t) => renderTrait(t))}</div>
                <h1 className={classes.sectionHeading}>Party Standing</h1>
                <div className={classes.statisticsBlock}>
                    <p>Reputation {party.reputation}</p>
                    {party.traits.map((t) => (
                        <p>{t}</p>
                    ))}
                </div>
                <h1 className={classes.sectionHeading}>Voormithadreth Status</h1>
                <div className={classes.statisticsBlock}>
                    {world.traits.map((t) => (
                        <p>{t}</p>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default HeroProfile;
